import { Box, Button, Typography } from "@rp/react/web/mui/core"
import { TrendingUp } from "@rp/react/web/mui/icons"

type Props = {
  entitiesCount: number
  onUpgrade: () => void
}

const WATCHLIST_MAX_ENTITIES_FREEMIUM_USERS = 5

function WatchlistDataTabsTableUnlockBanner({ entitiesCount, onUpgrade }: Props) {
  const isMax = entitiesCount > WATCHLIST_MAX_ENTITIES_FREEMIUM_USERS
  return (
    <Box
      sx={{
        position: "relative",
        width: "100%",
        height: 200,
        backgroundColor: "background.default",
        mt: isMax ? "-60px" : "-30px",
        zIndex: 10,
      }}
    >
      <Box
        sx={theme => ({
          position: "absolute",
          top: -60,
          height: "60px",
          width: "100%",
          zIndex: 1,
          background: `linear-gradient(0deg, ${theme.palette.surface.background} 0%, ${theme.palette.surface.background} 20%, rgba(0,0,0,0) 100%)`,
        })}
      />
      <Box sx={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
        <Typography sx={{ fontSize: 24, fontWeight: 700, mt: 2 }}>
          Unlock Full Financial Data
        </Typography>
        <Typography
          sx={theme => ({ fontSize: 15, color: theme.palette.textColor[700], mt: 2, mb: 1 })}
        >
          {isMax
            ? `View complete financial data for all ${entitiesCount} entities`
            : "View complete financial data for all your watchlist entities"}
        </Typography>
        <Button variant="contained" onClick={onUpgrade} startIcon={<TrendingUp />} sx={{ mt: 2 }}>
          Upgrade Now
        </Button>
      </Box>
    </Box>
  )
}

export { WatchlistDataTabsTableUnlockBanner, WATCHLIST_MAX_ENTITIES_FREEMIUM_USERS }
